.examples {
    padding: 30px 0 57px 0;


    &__title {
        font-weight: 400;
        font-size: 22px;
        margin-bottom: 30px;
    }
}


.example {
    display: flex;
    align-items: flex-start;
    flex: 1 ;
    border-bottom: 1px solid $gray;
    margin-bottom: 23px;
    padding-bottom: 23px;

    @include media($sm) {
        flex-direction: column;
        align-items: center;
    }

    &__img {
        width: 248px;
        // width: 100%;
        height: 180px;
        overflow: hidden;
        border-radius: 6px;
        flex-shrink: 0;
        margin-right: 18px;

        @include media($sm) {
            width: 100%;
            height: 100%;
            margin: 0;
            margin-bottom: 15px;
        }

        img {
            width: 100%;
        }
    }

    &__title {
        font-weight: 700;
        font-size: 30px;
        line-height: 30px;
        margin-bottom: 15px;
    }

    &__year {
        font-size: 20px;
        color: $gray;
        margin-bottom: 22px;
    }

    &__type {
        font-size: 18px;
        font-weight: 900;
        color: $white;
        background: $btn-blue;
        border-radius: 16px;
        padding: 0 10px;
        margin-right: 26px;

    }

    &__text {
        font-size: 16px;

    }

    
    
}
