.contact {

    &__title {
        margin-top: 110px;
        margin-bottom: 30px;
        font-size: 34px;
    }

    &__year {
        font-size: 20px;
        margin-bottom: 27px;
    }

    &__type {
        padding: 0 10px;
        background: $btn-red;
        border-radius: 16px;
        color: $white;
        font-weight: 900;
        font-size: 18px;
        margin-right: 17px;
    }

    &__text {
        font-size: 16px;
        margin-bottom: 40px;
    }

    &__img {
        width: 100%;
        height: 100%;
        margin-bottom: 10px;

        img {
            width: 100%;
        }
    }

    &__heading-1 {
        font-size: 30px;
        font-weight: 500;
        margin-top: 50px;
        margin-bottom: 10px;
    }

    &__heading-2 {
        font-size: 24px;
        font-weight: 500;
        margin-bottom: 20px;
    }
}