.recent {

    background: $bg-blue;
    padding: 30px 0;



    &__name {
        font-weight: 400;
        font-size: 22px;
    }

    &__link {
        font-size: 16px;
        color: $blue-font;
    }


    &__header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 15px;
    }

    &__body {
        @include row();
        @include media($sm) {
            justify-content: center;
        }
        
    }

    .col-6 {
        @include col(6);
        @include media($sm) {
            @include col(10);
            margin-bottom: 30px;
        }
        @include media($xs) {
            @include col(12);

        }

    }

    &__block {
        background: $white;
        padding: 24px 20px;
        height: 300px;
        overflow: auto;
    }

    &__title {
        font-weight: 700;
        font-size: 26px;
        margin-bottom: 30px;
    }

    &__date {
        font-size: 18px;
        margin-bottom: 20px;
    }

}