.blog {


    &__main-title {
        margin-top: 110px;
        margin-bottom: 50px;
    }

    &__title {
        font-size: 30px;
        font-weight: 500;
        margin-bottom: 10px;

        @include media($sm) {
            font-size: 26px;
        }
    }

    &__year {
        font-size: 20px;
        color: $gray;
        margin-bottom: 15px;
    }

    &__type {
        color: $dark-font;
    }

    &__text {
        font-size: 16px;
        
        &::after {
            content: '';
            display: block;
            height: 1px;
            background: $gray;
            margin: 30px 0;
        }
    }
}