@font-face {
  font-family: "Heebo";
  src: url(../fonts/Heebo/Heebo-Regular.ttf);
  font-weight: 400;
}

@font-face {
  font-family: "Heebo";
  src: url(../fonts/Heebo/Heebo-Medium.ttf);
  font-weight: 500;
}

@font-face {
  font-family: "Heebo";
  src: url(../fonts/Heebo/Heebo-Bold.ttf);
  font-weight: 700;
}

@font-face {
  font-family: "Heebo";
  src: url(../fonts/Heebo/Heebo-Black.ttf);
  font-weight: 900;
}

@font-face {
  font-family: "fontawesome";
  src: url(../fonts/fontawesome/fontawesome-duotone.ttf);
  font-weight: 900;
}

@font-face {
  font-family: "fontawesome";
  src: url(../fonts/fontawesome/fontawesome-light.ttf);
  font-weight: 300;
}

@font-face {
  font-family: "fontawesome";
  src: url(../fonts/fontawesome/fontawesome-brands.ttf);
  font-weight: 400;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

body {
  font-family: 'Heebo';
  color: #21243d;
}

.container {
  max-width: 856px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media (max-width: 1140px) {
  .container {
    max-width: 856px;
  }
}

@media (max-width: 856px) {
  .container {
    max-width: 768px;
  }
}

@media (max-width: 768px) {
  .container {
    max-width: 100%;
  }
}

@media (max-width: 480px) {
  .container {
    max-width: 100%;
  }
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

::after, ::before {
  font-family: 'fontawesome';
}

.icon-facebook::before {
  content: "\f082";
  font-size: 50px;
}

.icon-insta::before {
  content: "\f16d";
  font-size: 50px;
}

.icon-twitter::before {
  content: "\f099";
  font-size: 50px;
}

.icon-in::before {
  content: "\f7b0";
  font-size: 50px;
}

.main__title {
  font-weight: 700;
  font-size: 44px;
  color: #21243d;
}

@media (max-width: 768px) {
  .main__title {
    font-size: 30px;
  }
}

.download__link {
  font-weight: 500;
  font-size: 20px;
  color: white;
  padding: 10px 20px;
  background: #FF6464;
  display: inline-block;
  border-radius: 2px;
}

body.lock {
  overflow: hidden;
}

.header__nav {
  max-width: 1032px;
  display: flex;
  justify-content: flex-end;
  padding: 27px 20px;
}

@media (max-width: 768px) {
  .header__nav {
    padding: 10px 0;
  }
}

.header__burger {
  position: relative;
  width: 30px;
  height: 20px;
  display: none;
  z-index: 2;
  right: 10px;
}

@media (max-width: 768px) {
  .header__burger {
    display: block;
  }
}

.header__burger span {
  height: 2px;
  position: absolute;
  width: 100%;
  background: black;
  left: 0;
  top: 9px;
}

.header__burger::before, .header__burger::after {
  height: 2px;
  width: 100%;
  background: black;
  position: absolute;
  left: 0;
  content: '';
  transition: 0.5s;
}

.header__burger.active::before {
  transform: rotate(45deg);
  top: 9px;
  background: white;
}

.header__burger.active::after {
  transform: rotate(-45deg);
  bottom: 9px;
  background: white;
}

.header__burger.active span {
  display: none;
}

.header__burger::before {
  top: 0;
}

.header__burger::after {
  bottom: 0;
}

.menu__block {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  transition: 0.5s;
}

.menu__block:hover {
  background: rgba(255, 255, 255, 0.205);
}

.menu__body {
  transition: 0.5s;
}

.menu__body.active {
  top: 0;
}

@media (max-width: 768px) {
  .menu__body {
    position: fixed;
    left: 0;
    top: -100%;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.7);
  }
}

.menu__list {
  display: flex;
}

@media (max-width: 768px) {
  .menu__list {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-top: 100px;
  }
}

.menu__link {
  margin-left: 20px;
  font-size: 20px;
  font-family: 'Heebo';
  color: black;
  transition: 0.5s;
}

.menu__link:hover {
  color: #e76666;
}

@media (max-width: 768px) {
  .menu__link {
    font-size: 25px;
    display: inline-block;
    margin-bottom: 15px;
    margin-left: 0;
    color: white;
  }
}

.banner {
  padding-top: 80px;
  padding-bottom: 60px;
  margin-top: 150px;
}

@media (max-width: 768px) {
  .banner {
    margin-top: 33px;
  }
}

.banner__body {
  justify-content: space-between;
  align-items: center;
}

@media (max-width: 768px) {
  .banner__body {
    flex-direction: column-reverse;
    text-align: center;
  }
}

.banner__content {
  width: 58.33333%;
  padding-left: 15px;
  padding-right: 15px;
}

@media (max-width: 768px) {
  .banner__content {
    width: 66.66667%;
    padding-left: 15px;
    padding-right: 15px;
  }
}

@media (max-width: 480px) {
  .banner__content {
    width: 100%;
    padding-left: 15px;
    padding-right: 15px;
  }
}

.banner__img {
  max-width: 243px;
  border-radius: 50%;
  overflow: hidden;
}

@media (max-width: 768px) {
  .banner__img {
    margin-bottom: 34px;
  }
}

@media (max-width: 480px) {
  .banner__img {
    max-width: 174px;
  }
}

.banner__img img {
  max-width: 100%;
  display: block;
}

.banner__title {
  margin-bottom: 40px;
}

@media (max-width: 480px) {
  .banner__title {
    font-size: 32px;
  }
}

.banner__info {
  color: #21243d;
  font-size: 16px;
  margin-bottom: 38px;
}

.recent {
  background: #EDF7FA;
  padding: 30px 0;
}

.recent__name {
  font-weight: 400;
  font-size: 22px;
}

.recent__link {
  font-size: 16px;
  color: #00a8cc;
}

.recent__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
}

.recent__body {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

@media (max-width: 768px) {
  .recent__body {
    justify-content: center;
  }
}

.recent .col-6 {
  width: 50%;
  padding-left: 15px;
  padding-right: 15px;
}

@media (max-width: 768px) {
  .recent .col-6 {
    width: 83.33333%;
    padding-left: 15px;
    padding-right: 15px;
    margin-bottom: 30px;
  }
}

@media (max-width: 480px) {
  .recent .col-6 {
    width: 100%;
    padding-left: 15px;
    padding-right: 15px;
  }
}

.recent__block {
  background: white;
  padding: 24px 20px;
  height: 300px;
  overflow: auto;
}

.recent__title {
  font-weight: 700;
  font-size: 26px;
  margin-bottom: 30px;
}

.recent__date {
  font-size: 18px;
  margin-bottom: 20px;
}

.examples {
  padding: 30px 0 57px 0;
}

.examples__title {
  font-weight: 400;
  font-size: 22px;
  margin-bottom: 30px;
}

.example {
  display: flex;
  align-items: flex-start;
  flex: 1;
  border-bottom: 1px solid #8695a4;
  margin-bottom: 23px;
  padding-bottom: 23px;
}

@media (max-width: 768px) {
  .example {
    flex-direction: column;
    align-items: center;
  }
}

.example__img {
  width: 248px;
  height: 180px;
  overflow: hidden;
  border-radius: 6px;
  flex-shrink: 0;
  margin-right: 18px;
}

@media (max-width: 768px) {
  .example__img {
    width: 100%;
    height: 100%;
    margin: 0;
    margin-bottom: 15px;
  }
}

.example__img img {
  width: 100%;
}

.example__title {
  font-weight: 700;
  font-size: 30px;
  line-height: 30px;
  margin-bottom: 15px;
}

.example__year {
  font-size: 20px;
  color: #8695a4;
  margin-bottom: 22px;
}

.example__type {
  font-size: 18px;
  font-weight: 900;
  color: white;
  background: #142850;
  border-radius: 16px;
  padding: 0 10px;
  margin-right: 26px;
}

.example__text {
  font-size: 16px;
}

.footer {
  padding: 50px 0;
}

.footer__links {
  display: flex;
  justify-content: center;
  margin-bottom: 25px;
}

.footer__link {
  margin: 0 17px;
}

.footer__link::before {
  color: #21243d;
  font-size: 30px;
}

.footer__text {
  text-align: center;
  font-weight: 400;
  font-size: 14px;
}

.works__title {
  margin-top: 110px;
  margin-bottom: 50px;
}

@media (max-width: 768px) {
  .works__title {
    margin-top: 80px;
  }
}

@media (max-width: 768px) {
  .example__title {
    font-size: 24px;
  }
}

.blog__main-title {
  margin-top: 110px;
  margin-bottom: 50px;
}

.blog__title {
  font-size: 30px;
  font-weight: 500;
  margin-bottom: 10px;
}

@media (max-width: 768px) {
  .blog__title {
    font-size: 26px;
  }
}

.blog__year {
  font-size: 20px;
  color: #8695a4;
  margin-bottom: 15px;
}

.blog__type {
  color: #21243d;
}

.blog__text {
  font-size: 16px;
}

.blog__text::after {
  content: '';
  display: block;
  height: 1px;
  background: #8695a4;
  margin: 30px 0;
}

.contact__title {
  margin-top: 110px;
  margin-bottom: 30px;
  font-size: 34px;
}

.contact__year {
  font-size: 20px;
  margin-bottom: 27px;
}

.contact__type {
  padding: 0 10px;
  background: #FF6464;
  border-radius: 16px;
  color: white;
  font-weight: 900;
  font-size: 18px;
  margin-right: 17px;
}

.contact__text {
  font-size: 16px;
  margin-bottom: 40px;
}

.contact__img {
  width: 100%;
  height: 100%;
  margin-bottom: 10px;
}

.contact__img img {
  width: 100%;
}

.contact__heading-1 {
  font-size: 30px;
  font-weight: 500;
  margin-top: 50px;
  margin-bottom: 10px;
}

.contact__heading-2 {
  font-size: 24px;
  font-weight: 500;
  margin-bottom: 20px;
}
