$Montserrat:('Heebo','Regular', 400),
            ('Heebo','Medium', 500),
            ('Heebo','Bold', 700),
            ('Heebo','Black', 900),
            ('fontawesome','duotone', 900),
            ('fontawesome','light', 300),
            ('fontawesome','brands', 400),
            ;

@each $family, $name, $weight in $Montserrat {
    @font-face {
        font-family: $family;
        src: url(../fonts/#{$family}/#{$family}-#{$name}.ttf);
        font-weight: $weight;
    }
}


$hover-link:rgb(231, 102, 102) ;
$burger-bg: rgba(0, 0, 0, 0.7);
$bg-white: #E5E5E5;
$bg-blue: #EDF7FA;
$dark-font: rgba(33, 36, 61, 1);
$blue-font: rgba(0, 168, 204, 1);
$black: black;
$white: white;
$nav-white: rgba(255, 255, 255, 0.205);
$btn-red: #FF6464;
$btn-blue: #142850;
$gray: rgba(134, 149, 164, 1);