body.lock {
    overflow: hidden;
}


.header {


    &__nav {
        max-width: 1032px;
        display: flex;
        justify-content: flex-end;
        padding: 27px 20px;
        // background: red;
        
        @include media($sm) {
            padding: 10px 0;
        }

    }

    &__burger {
        position: relative;
        width: 30px;
        height: 20px;
        display: none;
        z-index: 2;
        right: 10px;
        


        @include media($sm) {
            display: block;
        }

        span {
            height: 2px;
            position: absolute;
            width: 100%;
            background: $black;
            left: 0;
            top: 9px;
        }

        &::before,&::after {
            height: 2px;
            width: 100%;
            background: $black;
            position: absolute;
            left: 0;
            content: '';
            transition: 0.5s;
        }

        &.active::before {
            transform: rotate(45deg);
            top: 9px;
            background: $white;
        }
        &.active::after {
            transform: rotate(-45deg);
            bottom: 9px;
            background: $white;
        }

        &.active span {
            display: none;
        }

        &::before {
            top: 0;
        }

        &::after {
            bottom: 0;
        }
    }
}


.menu {
    &__block {
        position: fixed;
        left: 0;
        top: 0;
        width: 100%;
        transition: 0.5s;

        &:hover {
            background: $nav-white;
        }
    }
    

    &__body {
        transition: 0.5s;

        &.active {
            top: 0;
        }

        @include media($sm) {
            position: fixed;
            // z-index: 10;
            left: 0;
            top: -100%;
            width: 100%;
            height: 100%;
            background: $burger-bg;
        }
    }


    &__list {
        display: flex;

        @include media($sm) {
            flex-direction: column;
            align-items: center;
            justify-content: center;
            padding-top: 100px;
        }
    }

    &__link {
        margin-left: 20px;
        font-size: 20px;
        font-family: 'Heebo';
        color: $black;
        transition: 0.5s;

        &:hover {
            color: $hover-link;
        }

        @include media($sm) {
            font-size: 25px;
            display: inline-block;
            margin-bottom: 15px;
            margin-left: 0;
            color: $white;
        }
        
    }
}