.banner {
    padding-top: 80px;
    padding-bottom: 60px;
    margin-top: 150px;

    @include media($sm) {
        margin-top: 33px;
    }


    &__body {
        // @include row();
        justify-content: space-between;
        align-items: center;

        @include media($sm) {
            flex-direction: column-reverse;
            text-align: center;
        }
    }

    &__content {
        @include col(7);


        
        @include media($sm) {
            @include col(8);
        }

        @include media($xs) {
            @include col(12);
        }
    }

    &__img {
        max-width: 243px;
        border-radius: 50%;
        overflow: hidden;

        @include media($sm) {
            margin-bottom: 34px;
        }

        @include media($xs) {
            max-width: 174px;
        }

        img {
            max-width: 100%;
            display: block;
        }
    }

    &__title {
        margin-bottom: 40px;

        @include media($xs) {
            font-size: 32px;
        }
    }

    &__info {
        color: $dark-font;
        font-size: 16px;
        margin-bottom: 38px;
    }
}

