*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

a{
    text-decoration: none;
}

ul {
    list-style: none;
}

body {
    font-family: 'Heebo';
    color: $dark-font;

}

.container {
    @include container();
}

.row {
    @include row();
}

::after, ::before {
    font-family: 'fontawesome';
}

.icon {
    &-facebook::before {
        content: "\f082";
        font-size: 50px;
    }
    &-insta::before {
        content: "\f16d";
        font-size: 50px;
    }
    &-twitter::before {
        content: "\f099";
        font-size: 50px;
    }
    &-in::before {
        content: "\f7b0";
        font-size: 50px;
    }
}

.main__title {
    font-weight: 700;
    font-size: 44px;
    color: $dark-font;

    @include media($sm) {
        font-size: 30px;
    }
}

.download__link {
    font-weight: 500;
    font-size: 20px;
    color: $white;
    padding: 10px 20px;
    background: $btn-red;
    display: inline-block;
    border-radius: 2px;
}