.footer {
    padding: 50px 0;

    &__links {
        display: flex;
        justify-content: center;
        margin-bottom: 25px;
    }

    &__link {
        margin: 0 17px;

        &::before {
            color: $dark-font;
            font-size: 30px;
        }
    }

    &__text {
        text-align: center;
        font-weight: 400;
        font-size: 14px;
    }
}